:root{
    --body-background: #252525;
    --font-col: #fff;
    --eclipse-col: #ffffff;
    --plus-icon-back: #C4C4C4;
    --logo-font: #FFC149;
    --logo: #FFD686;
    --item-back: #616161;
    --item-border: #C4C4C4;
    --item-font: #ffffff;
    --lock-back: #C4C4C4;
    --menu-itemcol: #464646;
}


[data-theme="dark"] {
    --body-background: #ffffff;
    --font-col: rgb(0, 0, 0);
    --eclipse-col: #020202;
    --plus-icon-back: #C4C4C4;
    --logo-font: #CF8900;
    --logo: #A66D00;
    --item-back: #E8E8E8;
    --item-border: #616161;
    --item-font: #000000;
    --lock-back: #C4C4C4;
    --menu-itemcol: #E8E8E8;
}

.parent{
    width: 100vw;
    overflow-x: hidden;
    height: 100vh;
    background-color: rgb(167, 167, 167);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.container{
    background-color: var(--body-background);
    flex: 1;
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

@media screen and (min-width:1109px)  {
    /* width */
    ::-webkit-scrollbar {
      width: 7px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
      background-color: transparent;
      transition: 1s ease;
    }
    ::-webkit-scrollbar-track:hover{
      background: var(--item-back); 
      border-radius: 7px;
      transition: 1s ease;
    }
    
    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: var(--font-col); 
      border-radius: 7px;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: var(--item-border); 
    }
}


@media screen and (max-width:1110px)  {
    .container{
        flex: 1;
        max-width: 100vw;
    }
}