.bigContainer{
    z-index: 999;
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.555);
    display: flex;
    justify-content: center;
    align-items: center;
}
.popupTask{
    position: fixed;
    padding-top: 3rem;
    bottom: 50%;
    left: 50%;
    width: 50vw;
    height: 75vh;
    border-radius: 10px;
    background-color:rgb(41, 41, 41) ;
    border: 2px solid grey;
    opacity: 1;
    transition: 0.2s all ease;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    visibility: visible;
    transform: translate(-50%, 50%);
}

.closebutn{
    position: absolute;
    height: 2rem;
    width: 2rem;
    top: 0;
    right: 0;
    background-color: rgb(182, 59, 59);
    border-bottom-left-radius: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 1rem;
}
.closebutn:active{
    background-color: rgb(136, 41, 41);
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.taskTitle{
    width: 90%;
}

.taskTitle input{
    width: 100%;
    height: 2rem;
    font-size: 1.2rem;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(78, 78, 78);
    color: rgb(255, 255, 255);
}
.description{
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.description textarea{
    width: 90%;
    height: 80%;
    font-size: 1.2rem;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(78, 78, 78);
    color: rgb(255, 255, 255);
}

.finalbtns{
    width: 100%;
    height: max-content;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
}
.finalbtns button{
    outline: none;
    border: black solid 2px ;
    color: rgb(0, 0, 0);
    height: 3rem;
    width: 6.3rem;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}
.finalbtns button:active{
    border: 1px solid white;
    color: white;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

@media screen and (max-width:1110px)  {
    .popupTask, .editTask{
        width: 96vw;
        height: 23rem;
    }
}