@import '../App.css';
.edit{
    flex: 0.45;
    background-color: var(--body-background);
    border: 1px solid var(--plus-icon-back);
    border-radius: 7px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    height: 38rem;
}

.inputKey{
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    left: 0;
    background-color: inherit;
}

.inputKey input{
    height: 2rem;
    margin-left: 1rem;
    width: 15rem;
    font-size: 1.2rem;
    font-weight: bold;
    outline: none;
    background-color: rgb(68, 68, 68);
    border-radius: 5px;
    border: 2px solid rgb(197, 197, 197);
    color: white;
}

.tools{
    height: 100%;
    width: 9rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 1rem;
}

.copyicon, .edticon, .delicon{
    height: 2.3rem;
    width: 2.3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.3rem;
    background-color: rgb(75, 75, 75);
    border-radius: 5px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.copyicon:active, .edticon:active, .delicon:active{
    background-color: rgb(122, 122, 122);
}


.copyicon i{
    color: white;
}
.delicon i{
    color: rgb(255, 107, 62);
}
.edticon i{
    color: rgb(69, 210, 253);
}

.decryptedMsg{
    width: 100%;
    font-size: 1.05rem;
    background-color: rgb(75, 75, 75);
    flex: 1;
    overflow-y: scroll;
}

.decryptedMsg pre{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    margin: 0.8rem;
    color: var(--font-col);
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    word-wrap: break-word;
}

.addbutnn button{
    width: 40rem;
    height: 3rem;
    margin-bottom: 0.5rem;
    font-size: 1.1rem;
    font-weight: bold;
    background-color: rgb(182, 59, 59);
    border: none;
    color: white;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.addbutnn button:active{
    background-color: rgb(128, 30, 30);
}

.containsinputs{
    width: 100%;
    background-color: rgb(44, 44, 44);
    position: sticky;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.clos{
    height: 100%;
    width: 90%;
    font-weight: bold;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.addbtnbar{
    display: none;
}
.menuitems{
    display: none;
    
}


@media screen and (max-width:1110px)  {
    .edit{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1111;
        flex: 1;
        border: none;
        border-radius: 7px;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        transform: translateX(100%);
        transition: 0.3s all ease;
    }
    .edit.active{
        transform: translateX(0%);
        transition: 0.3s all ease;
    }
    .inputKey input{
        height: 2rem;
        margin-left: 0.3rem;
        width: 10rem;
        font-size: 1.2rem;
        font-weight: bold;
        outline: none;
        background-color: rgb(68, 68, 68);
        border-radius: 5px;
        border: 2px solid rgb(197, 197, 197);
        color: white;
    }
    .tools{
        height: 100%;
        width: 8rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 0.5rem;
    }
    .closebutton{
        height: 2.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .closebutton button{
        font-size: 1.1rem;
        font-weight: bold;
        min-width: 4.6rem;
        height: 100%;
        background-color: grey;
        border: none;
    }
    .filenameDisplay2{
        font-size: 1.2rem;
        font-weight: bold;
        margin: 0rem;
        margin-left: 1rem;
        color: var(--plus-icon-back);
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
}